* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

header {
  padding-top: 10px;
  display: inline-block;
  width: 100%;
  text-align: center;
  height: 65px;
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.5);
  position: sticky;
  z-index: 999;
  top: 0;
  background-color: white ;
}

:root {
  --color-primary: #00CCFF;
  --br: .3rem;
  --form-border-color: rgb(225, 193, 255);
}

.container {
    width: 100%;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flex {
    width: fit-content;
    border: 1px solid var(--form-border-color);
    display: flex;
    align-items: center;
    border-radius: 999px;
    background-color: rgb(238, 238, 238);
    height: 50px;
    margin: 0 auto;
    margin-right:70px ;
}

.flex > input {
    margin-left: 10px;
    padding: 7px;
    border: none;
    outline: none;
    background-color: rgb(238, 238, 238);
    caret-color: rgb(185, 130, 236);
    color: rgb(99, 99, 99);
    width: 400px;

  }

  .header__logo{
    width: 50px;
    height: 50px;
    border-radius: 999px;
    background-color: black;
    margin-left: 60px;
}




